px 3px 6px #00000029;\n                border-radius: 10px;\n                padding: 8px;\n                word-break: break-word;\n\n                .call-help__suggestion-balloon-label {\n                  text-align: center;\n                  margin-top: -18px;\n\n                  span {\n                    padding: 0 8px;\n                    background-color: white;\n                    font-weight: 600;\n                  }\n                }\n              }\n\n              .call-help__dont-rec-call {\n                margin: 24px 0;\n                display: flex;\n                flex-direction: column;\n                gap: 8px;\n                align-items: center;\n\n                border: none;\n                box-shadow: 0px 3px 6px #00000029;\n                border-radius: 10px;\n                padding: 8px;\n\n                .dont-rec-call__help {\n                  margin-top: -16px;\n                  background: white;\n                  padding: 0 8px;\n                  font-weight: 600;\n                }\n\n                .dont-rec-call__add-contact {\n                  display: flex;\n                  flex-direction: row;\n                  gap: 4px;\n                }\n\n                /*.dont-rec-call__contacts,\n                .dont-rec-call__label-contacts {\n                  align-self: flex-start;\n                }*/\n              }\n\n              .call-help__carousel-item-incorrect-contact {\n                display: flex;\n                justify-content: space-between;\n\n                .el-select {\n                  flex: 3;\n                }\n\n                .el-button {\n                  flex: 1;\n                  height: 40px;\n                }\n              }\n\n              .call-help__carousel-item-actions {\n                display: flex;\n                flex-direction: row;\n                justify-content: space-around;\n\n                .call-help__carousel-item-actions__correct_contact {\n                  span {\n                    display: flex;\n                    align-items: center;\n                    gap: 8px;\n                  }\n                }\n              }\n            }\n          }\n        }\n      }\n    }\n  }\n}\n\n.incorrect-contact__popover,\n.dont-rec-call__popover {\n  padding: 16px !important;\n  display: flex;\n  flex-direction: column;\n  gap: 16px;\n\n  .el-popover__title {\n    margin: 0;\n    font-weight: 600;\n    text-align: center;\n  }\n\n  .dont-rec-call__label-contacts {\n    font-weight: 600;\n    text-transform: capitalize;\n    margin-bottom: -16px;\n    font-size: 14px;\n  }\n\n  .dont-rec-call__contacts {\n    margin-bottom: -16px;\n  }\n\n  .dont-rec-call__contacts:last-of-type {\n    margin-bottom: 16px;\n  }\n\n  .dont-rec-call__add-contact {\n    display: flex;\n    flex-direction: row;\n    gap: 8px;\n  }\n}\n"],"sourceRoot":""}]);
